<template>
  <div>
    <el-card class="table-card">
      <div slot="header" class="header">
        <span>角色管理</span>
        <el-button @click="insert_dialog_visible = true" icon="el-icon-circle-plus-outline" plain type="primary">
          添加角色
        </el-button>
      </div>
      <el-table :data="roles">
        <el-table-column prop="role_id" label="#" width="60">
        </el-table-column>
        <el-table-column prop="role_name" label="角色名称">
          <template slot-scope="scope">
            <el-tag>{{ scope.row.role_name }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="role_code" label="角色代码">
        </el-table-column>
        <el-table-column prop="role_description" label="角色描述">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button @click="openEditDialog(scope.row)" icon="el-icon-edit" plain
              type="primary">
              编辑
            </el-button>
            <el-button :disabled="scope.row.editable === 0" @click="handleRemoveRole(scope.row)"
              icon="el-icon-delete" plain type="danger">
              删除
            </el-button>
            <el-link :href="`#/system/role/${scope.row.role_id}`" :underline="false" class="am-margin-left-sm">
              <el-button :disabled="scope.row.editable === 0" type="warning" plain icon="el-icon-setting">
                权限
              </el-button>
            </el-link>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 添加角色 -->
    <el-dialog title="添加角色" :visible.sync="insert_dialog_visible" @close="handleDialogClose('insert_form')">
      <el-form ref="insert_form" :rules="rules" :model="insert_form" label-width="80px">
        <el-form-item label="角色名称" prop="role_name">
          <el-input v-model="insert_form.role_name"></el-input>
        </el-form-item>
        <el-form-item label="角色代码" prop="role_code">
          <el-input v-model="insert_form.role_code" placeholder="请输入大写英文的角色代码"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="role_description">
          <el-input v-model="insert_form.role_description" placeholder="请输入角色的说明文字"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="insert_dialog_visible = false">取 消</el-button>
        <el-button @click="handleInsertRole" type="primary">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 编辑角色 -->
    <el-dialog title="编辑角色" :visible.sync="edit_dialog_visible" @close="handleDialogClose('edit_form')">
      <el-form ref="edit_form" :rules="rules" :model="edit_form" label-width="80px">
        <el-form-item label="角色名称" prop="role_name">
          <el-input v-model="edit_form.role_name"></el-input>
        </el-form-item>
        <el-form-item label="角色代码" prop="role_code">
          <el-input v-model="edit_form.role_code" placeholder="请输入大写英文的角色代码"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="role_description">
          <el-input v-model="edit_form.role_description" placeholder="请输入角色的说明文字"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="edit_dialog_visible = false">取 消</el-button>
        <el-button @click="handleEditRole" type="primary">保 存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'

export default {
  name: "Role",
  data() {
    return {
      insert_dialog_visible: false,
      insert_form: {
        role_name: '',
        role_code: '',
        role_description: '',
      },
      edit_dialog_visible: false,
      edit_form: {
        role_id: '',
        role_name: '',
        role_code: '',
        role_description: '',
      },
      //当前操作行row的角色
      current_role: '',
      rules: {
        role_name: [{ required: true, message: "请输入角色名称", trigger: 'blur' }],
        role_code: [{ required: true, message: "请输入大写英文的角色代码", trigger: 'blur' }],
        role_description: [{ required: true, message: "请输入角色的说明文字", trigger: 'blur' }],
      },
    }
  },
  created() {
    document.title = "角色管理";
    // 角色列表
    this.load_role_list();
  },
  computed: {
    ...mapState('Role', ['roles']),
  },
  methods: {
    // 加载所有 action
    ...mapActions('Role', ['load_role_list', 'create_role', 'edit_role', 'remove_role']),
    // 添加角色
    handleInsertRole() {
      this.$refs.insert_form.validate(async (valid) => {
        if (valid) {
          let { status, msg, data } = await this.create_role({ ...this.insert_form });
          if (status) {
            // 关闭弹窗
            this.insert_dialog_visible = false;
            // 消息提示
            this.$message.success(msg);
          } else {
            // 消息提示
            this.$message.error(msg);
          }
        }
      });
    },
    // 打开编辑弹窗
    openEditDialog(role) {
      // 缓存角色指针地址
      this.current_role = role;
      // 还原表单
      this.edit_form = { ...role };
      // 打开 dialog
      this.edit_dialog_visible = true;
    },
    // 编辑角色
    handleEditRole() {
      this.$refs.edit_form.validate(async (valid) => {
        if (valid) {
          let { status, msg } = await this.edit_role({ ...this.edit_form });
          if (status) {
            // 关闭弹窗
            this.edit_dialog_visible = false;
            // 信息提示
            this.$message.success(msg);
          } else {
            // 信息提示
            this.$message.error(msg);
          }
        }
      });
    },
    // 删除角色
    async handleRemoveRole(role) {
      this.$confirm('此操作将永久删除该角色, 是否继续?', { type: 'warning' })
        .then(async () => {
          // 删除角色
          let { status, msg } = await this.remove_role(role);
          if (status) {
            // 信息提示
            this.$message.success(msg);
          } else {
            // 信息提示
            this.$message.error(msg);
          }
        })
        .catch(() => {
          this.$message.info('取消成功！');
        });
    },
    // 关闭弹窗
    handleDialogClose(form) {
      this.$refs[form].resetFields();
    },
  }
}
</script>

<style lang="less">
.table-card {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
</style>
